// TEAM LEFT
.team-containerleft {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: rgb(255, 255, 255);
    background-color: #111111;
    height: 200px;
}

.team-logoembed img {
    width: 500px;
    height: 500px;
}

.team-nameembed h2 {
    font-family: "Rubik", sans-serif;
    position: relative;
    bottom: 40px;
    left: 100px;
    font-size: 80px;
    font-weight: 900;
    letter-spacing: -8px;
}
.team-capitainesembed p {
    font-family: "Rubik", sans-serif;
    font-style: italic;
    position: relative;
    bottom: 36px;
    left: 115px;
    font-size: 30px;
    font-weight: 600;
    letter-spacing: -1px;
}
.team-playersembed p {
    position: relative;
    top: 55px;
    right: 350px;
    font-size: 30px;
    font-weight: 600;
    letter-spacing: -1px;
}

// SPACE BETWEEN
.spacebetween {
    padding-top: 50px;
    padding-bottom: 50px;
}

