/* Form.scss */
* {
    font-family: 'Montserrat', sans-serif;
}

.form {
    margin: 0 auto;
    padding: 20px;
    border-radius: 5px;
    font-size: 19px;
  }
  
  .form h2 {
    font-weight: 300;
    font-size: 32px;
    color: #FF00F5;
    margin-bottom: 20px;
    text-align: left;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 5px;
    color: white;
    font-weight: 600;
  }

  input[type=text] {
    padding-left: 7px;
    font-size: 19px;
  }
  input[type=email] {
    padding-left: 7px;
    font-size: 19px;
  }
  textarea[id=comment] {
    width: 618px;
    height: 100px;
    padding-left: 7px;
    font-size: 19px;
  }
  
  
  button {
    margin-top: 10px;
  }
  
  .button-53 {
    background-color: #3DD1E7;
    border: 0 solid #E5E7EB;
    box-sizing: border-box;
    color: #000000;
    display: flex;
    font-size: 1rem;
    font-weight: 700;
    justify-content: center;
    line-height: 1.75rem;
    padding: .75rem 1.65rem;
    position: relative;
    text-align: center;
    text-decoration: none #000000 solid;
    text-decoration-thickness: auto;
    max-width: 460px;
    position: relative;
    cursor: pointer;
    transform: rotate(-2deg);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
  }
  
  .button-53:focus {
    outline: 0;
  }
  
  .button-53:after {
    content: '';
    position: absolute;
    border: 1px solid #ffffff;
    bottom: 4px;
    left: 4px;
    width: calc(100% - 1px);
    height: calc(100% - 1px);
  }
  
  .button-53:hover:after {
    bottom: 2px;
    left: 2px;
  }
  
  @media (min-width: 768px) {
    .button-53 {
      padding: .75rem 3rem;
      font-size: 1.25rem;
    }
  }
