.navbarmohaa {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-size: 1.4rem;
    font-family: "Montserrat", sans-serif;
    z-index: 1;

    a {
        text-decoration: none;
        color: orange;
        font-style: normal;
        font-weight: 700;
        padding: 5px; 
        border-radius: 5px; 
        transition: background-color 0.3s ease;
        @media only screen and (max-width: 768px) {
            text-transform: uppercase;
            font-size: 0.8em;
            font-weight: 500;
            min-width: 3.5rem;
        }  
    }

    a:hover {
        color: #E66108; 
    }

    .separator {
        color: white; 
        font-size: 70px; 
        font-weight: 100;
        margin: 0 10px; 
    }

    @media screen and (max-width: 768px) {
        flex-direction: column;
        gap: 2%; 
        padding: 10px;
    }

    @media screen and (max-width: 420px) {
        padding: 5px;
        gap: 3%; 
    }
}
