.classement-playercod1{
    padding-top: 15px;
    color: white;
    text-align: center;
}
.player-controls {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.button-31 {
  border-radius: 20px;
  background-color: #222;
  border-style: none;
  color: #f1b100;
  cursor: pointer;
  display: flex;
  justify-content: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
  max-width: 300px;
  min-height: 44px;
  min-width: 10px;
  outline: none;
  padding: 9px 20px 8px;
  user-select: none;
  -webkit-user-select: none;
  width: 100%;
}

.button-31:hover,
.button-31:focus {
  opacity: .75;
}