.rules-mohaa {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .collapse {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    margin-bottom: 10px;
  }
  
  .collapse-label {
    font-weight: bold;
    margin-right: 10px;
  }
  
  .img-collapse {
    max-width: 70%;
    margin-top: 10px;
    opacity: 1; /* Définit l'opacité initiale de l'image */
    height: auto; /* Assurez-vous que la hauteur de l'image est automatique */
    transition: opacity 3.3s ease; /* Ajoute une transition pour une animation fluide */
  }
  
  .collapse.closed .img-collapse {
    opacity: 0; /* Cache l'image lorsque le collapse est fermé */
    height: 0; /* Réduit la hauteur de l'image à zéro lorsque le collapse est fermé */
    overflow: hidden; /* Cache tout contenu dépassant de la zone de l'image */
  }
  