
.server-alpha {
    position: relative;
    display: inline-block;
    margin-left: 250px;
    left: -130px;
  }
  
  .server-alpha img {
    display: block;
    width: 100%;
    height: auto;
  }
  
  .map-image {
    position: absolute;
    z-index: -1;
    width: 250px!important; 
    height: 207px!important; 
    top: -280%;
    left: 3%;
  }
  
  .text-overlay {
    position: absolute;
    top: 72%; 
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    text-align: center;
  }
  
  .map-template {
    font-size: 24px;
    margin: 0;
  }
  
  .map-name {
    font-size: 18px;
    margin: 0;
  }
  
  .gamemode {
    font-size: 16px;
    margin: 0;
  }
  
  .ipserver {
    font-size: 14px;
    margin: 0;
    white-space: nowrap; // Empêche le texte de se briser sur plusieurs lignes
  }
  
  
  .playersonline {
    font-size: 12px;
    margin: 0;
  }
  