.navbar {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 ;
    gap: 2rem;
    /* margin: 0 auto; */
    color: var(--red);
    font-weight: 400;
    min-width: 20%;
    margin-right: 2px;
    font-style: normal;
    font-size: 24px;
    font-family: "Montserrat", sans-serif;
    @media screen and (width <= 420px) {
        flex-direction: column; 
    }

    a {
        text-decoration: none;
        color: black;
        font-style: normal;
        font-weight: 700;
        @media only screen and (width <= 768px) {
            text-transform: uppercase;
            font-size: 12px;
            font-weight: 500;
            min-width: 3.5rem;
        }  
    }

    :hover {
        color: #E66108;
    }
}