// Footer.scss
* {
    text-decoration: none;
}
body {
  background-color: black;
}
.footer {
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #181818;
    color: #fff;
    padding: 20px;
    @media screen and (width <= 768px) {
      flex-direction: column;
  }
  
    &__right {
      font-size: 20px;
      text-align: center;

      .container__logo {
        display: flex;
        justify-content: center;
        gap: 30px;
        padding-bottom: 60px;
      }
    }
  }
  
.link a {
    color: white!important;
  }



  .footer {
    background: #ac9a78; /* La couleur de votre fond de pied de page */
    color: rgb(0, 0, 0);
    padding: 10px 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    }
    
    .footer-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 10px;
    font-weight: bold;
    }
    
    .social-media {
    display: flex;
    font-weight: bold;
    
    }
    .social-media a {
    display: inline-flex; /* Pour aligner les icônes en ligne avec un espace autour */
    flex-direction: row;
    align-items: center;
    margin: 0 5px;
    }
    
    .social-media a img {
    width: 30px; /* Ou la taille de vos icônes */
    height: auto;
    }
    
    .footer-section a {
    display: flex;
    align-items: center;
    color: rgb(0, 0, 0); /* Couleur du texte des liens */
    text-decoration: none; /* Pas de soulignement pour les liens */
    }
    
    .footer-section a img {
    margin-right: 5px; /* Espace entre l'icône et le texte */
    }