.title-section {
    display: flex;
    justify-content: center;
    font-size: 2.5rem;
    color: black;
  }
  .points-system {
    display: flex;
    justify-content: center;
  }
  .average-table-section {
    padding: 20px;
    padding-bottom: 500px;
  }
  
  .average-table {
    width: 100%;
    border-collapse: collapse;
    text-align: center;
  }
  .border-none {
    border: 0px!important;
  }
  .border-top {
    border-top: 0px!important;
  }
  .border-total {
    border-right: 0px!important;
    border-top: 0px!important;
    color: #e66108!important;
  }
  .border-left {
    border-left: 0px!important;
    font-size: 27px;
  }
  
  .total-average {
    border-right: 0px!important;
  }
  .average-table thead th {
    font-size: 27px;
    font-weight: bold;
    color: #000000;
    padding: 10px;
    border: 2px solid #000000; 
  }
  
  
  .average-table tbody td {
    font-size: 31px;
    font-weight: bold;
    border: 2px solid #000000;
    padding: 10px;
  }
  
  .team-icon {
    width: 170px; 
    height: 170px;
    margin-right: 5px;
    border-radius: 20px;
    vertical-align: middle;
  }

  td {
    color: black;
  }