.phase-one {
    text-align: center;
    padding: 20px;
  }
  
  .phase-header {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  .phase-header h2 {
    color: black;
    display: inline-block;
    font-size: 2em;
    margin-right: 10px;
  }
  
  .phase-header .map-name {
    font-size: 1em;
    vertical-align: middle;
  }
  
  
  .match-making {
    display: flex;
    align-items: center;
    justify-content: center;
  
    background-color: #eddcbc;
  }
  
  .match-making.url {
    padding-top: 80px;
    margin-bottom: 20px;
  }
  
  .team-making {
    flex-grow: 1;
    color: rgb(0, 0, 0);
    font-weight: bold;
    text-align: center;
    margin: 0 10px;
  }
  
  .vs-score-container {
    display: flex;
    align-items: center;
    position: relative;
  }
  
  .score {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px; 
    height: 60px;
    color: white;
    font-size: 1.5em;
    border-radius: 10px;
    background-size: cover; 
  }
  
  .vs {
    font-size: 2em; 
    color: #000000;
    font-weight: bold;
    margin: 0 20px;
    z-index: 1; 
  }

  .logo-match {
    width: 70px;
    border-radius: 50%;
  }
  .left-team {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
  
  }
  .right-team {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
  
  }