.letterspace {
    font-family: "Rubik", sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 300px;
    font-weight: 300;
    letter-spacing: -45px;
}

.img-container {
    display: flex;
    justify-content: center;
    padding-top: 100px;
    padding-bottom: 50px;
  }
  
  .match-mohaa {
    position: relative;
  
    .img-container {
      cursor: pointer;
    }
  
    .match-content {
      overflow: hidden;
      transition: height 0.3s ease; /* Définir la transition de hauteur */
    }
  
    &.collapsed {
      .match-content {
        height: 0; /* Réduire la hauteur à 0 lorsqu'il est collapsé */
      }
    }
  }
  