.container-demi {
  background-color: #FAF0DE;
}

.demi-finale {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #e66108;
    font-size: 50px;
  }
  
  .match-title {
    font-size: 40px;
    padding-bottom: 50px;
    text-align: center;
    font-weight: bold;
    margin-bottom: 10px;
    color: black;
  }
  .tournament {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    margin: 20px;
  }
  
  .match {
    display: flex;
    flex-direction: column;
    border: 2px solid #000;
    border-radius: 30px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    padding: 10px;
  }
  
  .team {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 100px;
  }
  
  .team-name, .score {
    margin: 0 5px;
  }
  
  .score {
    padding: 10px 10px;
    border-radius: 5px;
    color: white;
    font-weight: bold;
    border: 2px solid black;
    background-color: #a7a5a5;
  }
  .score.red {
    padding: 10px 10px;
    border-radius: 5px;
    background-color: #f00;
  }
  .score.green {
    padding: 10px 10px;
    border-radius: 5px;
    background-color: #0f0;
  }
  
  a.url {
    font-size: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: -62px;
    padding-bottom: 15px; 
  }
  
  .urld {
    font-size: 20px;
  }