.cod1-teams {
  background-color: #FAF0DE;
}
  .navbar {
    position: absolute;
    top: 90%; 
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .banner {
    position: relative;
    top: -80px;
    width: 100%;
  }
  
  
  /* SECTION TEAMS */
  .cod1-teams {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%; /* La bannière prend toute la largeur */
    height: auto; /* Hauteur automatique pour garder le ratio de l'image */
    padding-bottom: 50px;
    position: relative;
    top: 3px;

  }
  .teams {
    padding: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around; /* Assure un espacement uniforme */
    gap: 20px; /* Espace entre les éléments */
  }
  
  .container{
    user-select: none;
    /* margin: 100px auto; */
    background: url(https://cdn.pixabay.com/photo/2016/08/09/16/02/camouflage-1581003_960_720.jpg);
    color: #000000;
    border-radius: 20px;
    width: 350px;
    text-align: center;
    box-shadow: 0 10px 20px -10px rgba(0,0,0,.75);
  }
  .cover-photo{
    height: 160px;
    width: 100%;
    border-radius: 5px 5px 0 0;
  }
  .profile{
    height: 170px!important;
    width: 170px!important;
    border-radius: 50%;
    margin: 0 0 0 0;
    padding: 7px;
  }
  .profile-name{
    font-size: 25px;
    font-weight: bold;
    margin: 27px 0 0 0;
  }
  .about{
    display: flex;
    flex-direction: column;
    color: white;
    background-color: #42423c;
    border-radius: 10px;
    margin-top: 35px;
    line-height: 21px;
  }
 