.final {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #e66108;
    font-size: 50px;
    border: 2px solid #000;
    border-radius: 30px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    margin: 20px;
    padding: 20px;
  }
  
  .final-title {
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #e66108;
    font-size: 50px;
  }
  
  .team-section {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 10px;
  }
  
  .team-logo {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-size: cover;
    background-position: center;
    margin-right: 10px;
  }
  
  .team-info {
    display: flex;
    flex-direction: row;
    text-align: center;
    align-items: center;
  }
  
  .team-name {
    padding-right: 10px;
    padding-left: 10px;
    color: black;
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .team-finalcount {
    padding: 5px 5px;
    border-radius: 5px;
    border: 2px solid black;
    color: #fff;
    font-weight: bold;
    font-size: 30px;
  }
  
  .team-finalcount.red {
    background-color: #f00;
  }
  
  .team-finalcount.green {
    background-color: #0f0;
  }
  
  .versus {
    padding-left: 10px;
    padding-right: 10px;
    font-size: 30px;
    font-weight: bold;
    margin: 20px 0;
  }